<template>
    <div class="detail">
        <!-- header -->
        <Topbar :toptitle="'详情'"></Topbar>

        <!-- swiper -->
        <div class="swiper">
            <mt-swipe :auto="0">
                <mt-swipe-item><img src="../assets/goods1.jpg" alt=""></mt-swipe-item>
                <mt-swipe-item><img src="../assets/goods2.jpg" alt=""></mt-swipe-item>
                <mt-swipe-item><img src="../assets/goods1.jpg" alt=""></mt-swipe-item>
              </mt-swipe>
        </div>

        <!-- 价格 -->
        <div class="detail_price">
            <p>
                <span class="ze">￥69</span>
                <span class="yuan">原价：￥229</span>
            </p>
        </div>
        <!-- 名称 -->
        <div class="detail_title">
            <p>【满口麻香】向妈妈年货大礼包组合,向妈妈年货大礼包组合,向妈妈年货大礼包组合向妈妈年货大礼包组合</p>
        </div>

        <!-- 选项卡 -->
