            </div>
        </div>
        <div :style="{'display' : [tab == 1 ? 'block' : 'none']}" class="detail_picture">
            <img src="../assets/detail1.jpg" alt="">
            <img src="../assets/detail2.jpg" alt="">
            <img src="../assets/detail3.jpg" alt="">
            <img src="../assets/detail4.jpg" alt="">
            <img src="../assets/detail5.jpg" alt="">
        </div>
        <div :style="{'display' : [tab == 2 ? 'block' : 'none']}" class="canshu">
