            <p><span>划线价：</span>商品展示的划横线价格为商品参考价，该价格可能是品牌的专柜标价、商品的吊牌价或由品牌供应商所提供的正品原价（如厂商指导价、建议原价等）或商品在本站上曾经展示过的销售价；因存在地区、时间的差异性和市场行情上的波动，品牌的专柜标价、商品的吊牌价等可能会与您购物时展示的不一致，该价格仅供您参考。</p>
            <p><span>折扣：</span>如无特殊说明，折扣指销售商在商品划线价（如品牌的专柜标价、商品的吊牌价、厂商指导价、建议原价）等某一价格基础之上计算出的优惠比例或优惠金额；如有疑问，建议您在购买前联系商家客服进行咨询。</p>

        </div>

        <!-- 详情、参数 -->
        <!-- <div class="xiangqing">
            <p ref='xq' class="active" @click='xiangqing'>宝贝详情</p>
            <p ref='cs' @click='canshu' >规格参数</p>
        </div> -->


        <div class="foot"></div>
        <!-- 立即购买 -->
        <router-link :to="{ name:'Order' }">
            <div class="buy">立即购买</div>
        </router-link>
    </div>
</template>

<script>
export default {
  name: 'Home',
  data () {
    return {
      msg: 'Welcome to Your Vue.js App',
      tab: 1
    }
  },
  methods: {
    // xiangqing(){
    //     this.$refs.xq.setAttribute('class','active')
    //     this.$refs.cs.setAttribute('class','')
    //     this.$refs.xiangq.style.display='block'
    //     this.$refs.cans.style.display='none'
    // }   
  }
}
 
</script>

<style scoped>
    .detail{
        padding-top: 40px;
    }
.detail >>> .mint-header .mint-button .mint-button-text{
  font-size: 0.85rem;
}

    .swiper{
        /* padding: 0 10px; */
        height: 280px;
        background-color: seagreen;
    }
    .swiper img{
        width: 100%;
        height: 100%;
    }
    .detail_price{
        width: 100%;
        padding: 10px;
        background-color: rgb(171, 203, 231);
    }
    .ze{
        font-size: 1.375rem;
        color: rgb(241, 40, 4);
    }
    .yuan{
        font-size: 0.755rem;
        color: rgb(46, 46, 46);
        text-decoration: line-through;
    }
    /* 商品标题 */
    .detail_title{
        padding: 5px;
    }
    .detail_title p{
        font-size: 0.85rem;
    }

    .guige{
        background-color: #f8f8f8;
        padding: 5px 0;
    }
    .mint-cell {
        min-height: 33px;
        font-size: 0.75rem;
        color: #666;
    }

    .xiangqing{
        width: 100%;
        margin: auto;
        /* height: 300px; */
        /* padding: 10px ; */
        /* background-color: rgb(233, 232, 232); */
        /* 设置为弹性容器 */
        display: flex;
        /* 换行，第一行在上方 */
        flex-wrap: wrap;
        /* 间隔相等 ，自动分配距离*/
        justify-content: space-around;
        align-items: center; 
    }
    .xiangqing p{
        color: #666;
        padding: 10px ;
        text-align: center;
        flex-basis: 45%;
        flex-grow: 1;
        /* height: 150px; */
        /* background-color: deepskyblue; */
    }
    .xiangqing .active{
        color: black;
        border-bottom: 2px solid blue;
    }


    /* 详情图片 */
    .detail_picture{
        background-color: rgb(229, 255, 0);
        margin-bottom: 50px;
        padding-bottom: 50px;
    }
    .detail_picture img{
        width: 100%;
        float: left;
    }
    /* 规格参数 */
    .canshu{
        padding: 10px;
        display: none;
    }
    .canshu p{
        font-size: 0.85rem;
        margin: 5px 0;
        line-height: 20px;
        color: #444;
        position: relative;
    }
    .canshu p::before{
        content: '';
        width: 2px;
        height: 12px;
        position: absolute;
        left: -5px;
        top: 5px;
        background-color: #ddd;
    }
    .canshu p span{
        color: black;
        font-size: 0.85rem;
        font-weight: 600;
    }



    .foot{
        clear: both;
        height: 50px;
        background-color: thistle;
    }
    .buy{
        width: 100%;
        height: 50px;
        line-height: 50px;
        text-align: center;
        position: fixed;
        bottom: 0;
        left: 0;
        background-color: rgb(235, 69, 66);
        color: rgb(255, 255, 255);
    }
    .buy a{
        font-weight: 600;
        color: rgb(255, 255, 255);
    }
</style>



